import React, { useState } from "react";
import axios from 'axios';
import { Form, Button, Checkbox } from "semantic-ui-react";
import DatePicker from 'react-datepicker'
import "react-datepicker/dist/react-datepicker.css";
import { Line } from 'react-chartjs-2';
import 'bootstrap/dist/css/bootstrap.min.css';


export default function DataForm() {

    const [start_date, setStartDate] = useState();
    const [end_date, setEndDate] = useState();
    const [covidCases, setCovidCases] = useState([]);
    const [covidAdmissions, setCovidAdmissions] = useState([]);
    const [covidDeaths, setCovidDeaths] = useState([]);
    const [dates, setDates] = useState([]);
    const [casesMedian, setCasesMedian] = useState();
    const [admissionsMedian, setAdmissionsMedian] = useState();
    const [deathsMedian, setDeathsMedian] = useState();
    const [casesMean, setCasesMean] = useState();
    const [admissionsMean, setAdmissionsMean] = useState();
    const [deathsMean, setDeathsMean] = useState();
    const [timeStamp, setTimeStamp] = useState();

    const [dateEdit, setDateEdit] = useState();
    const [casesEdit, setCasesEdit] = useState();
    const [admissionsEdit, setAdmissionsEdit] = useState();
    const [deathsEdit, setDeathsEdit] = useState();

    const [dateAdd, setDateAdd] = useState();
    const [casesAdd, setCasesAdd] = useState();
    const [admissionsAdd, setAdmissionsAdd] = useState();
    const [deathsAdd, setDeathsAdd] = useState();

    //var url = "http://localhost:8080"
    var url = ""
    function getAllData() {
        if (start_date && end_date) {
            axios.get(url + '/api/getData', {
                params: {
                    startDate: start_date.toLocaleDateString("en", values),
                    endDate: end_date.toLocaleDateString("en", values)
                },
                headers: { "Content-Type": "application/json" },
            }).then((response) => {
                let casesArray = response.data.map((item) => {
                    return item.cases;
                })
                setCovidCases(casesArray)
                let admissionsArray = response.data.map((item) => {
                    return item.admissions;
                })
                setCovidAdmissions(admissionsArray)

                let deathsArray = response.data.map((item) => {
                    return item.deaths;
                })
                setCovidDeaths(deathsArray)

                let datesArray = response.data.map((item) => {
                    return item.date;
                })
                setDates(datesArray)

            });
        } else {
            axios.get(url + '/api/getAllData', {
                headers: { "Content-Type": "application/json" }
            }).then((response) => {
                console.log(typeof response.data)
                let casesArray = response.data.map((item) => {
                    return item.cases;

                })
                setCovidCases(casesArray)
                let admissionsArray = response.data.map((item) => {
                    return item.admissions;
                })
                setCovidAdmissions(admissionsArray)

                let deathsArray = response.data.map((item) => {
                    return item.deaths;
                })
                setCovidDeaths(deathsArray)

                let datesArray = response.data.map((item) => {
                    return item.date;
                })
                setDates(datesArray)

            });
        }

    }

    function getMean() {
        if (start_date && end_date) {
            axios.get(url + '/api/getMean', {
                params: {
                    startDate: start_date.toLocaleDateString("en", values),
                    endDate: end_date.toLocaleDateString("en", values)
                },
                headers: { "Content-Type": "application/json" },
            }).then((response) => {
                console.log(response)
                setCasesMean(response.data.cases)
                setAdmissionsMean(response.data.admissions)
                setDeathsMean(response.data.deaths)
            });
        } else {
            axios.get(url + '/api/getAllMeans', {
                headers: { "Content-Type": "application/json" }
            }).then((response) => {
                console.log(response)
                setCasesMean(response.data.cases)
                setAdmissionsMean(response.data.admissions)
                setDeathsMean(response.data.deaths)
            });
        }

    }

    function getMedian() {
        if (start_date && end_date) {
            axios.get(url + '/api/getMedian', {
                params: {
                    startDate: start_date.toLocaleDateString("en", values),
                    endDate: end_date.toLocaleDateString("en", values)
                },
                headers: { "Content-Type": "application/json" },
            }).then((response) => {
                console.log(response)
                setCasesMedian(response.data.cases)
                setAdmissionsMedian(response.data.admissions)
                setDeathsMedian(response.data.deaths)
            });
        } else {
            axios.get(url + '/api/getAllMedian', {
                headers: { "Content-Type": "application/json" }
            }).then((response) => {
                console.log(response)
                setCasesMedian(response.data.cases)
                setAdmissionsMedian(response.data.admissions)
                setDeathsMedian(response.data.deaths)
            });
        }

    }

    function delteData() {
        var date = timeStamp.toLocaleDateString("en", values)
        var formatedDate = date.replaceAll("/", "-")
        axios.delete(url + '/api/deleteData/' + formatedDate, {
            
            headers: { "Content-Type": "application/json" },
        })
    }

    function alterData() {
        var date = dateEdit.toLocaleDateString("en", values)
        var formatedDate = date.replaceAll("/", "-")
        axios.put(url + '/api/alterData/' + formatedDate , {"cases" : casesEdit, "admissions" : admissionsEdit, "deaths" : deathsEdit}
             
        )
        console.log({"cases" : casesEdit, "admissions" : admissionsEdit, "deaths" : deathsEdit})
    }

    function addData() {
        var date = dateAdd.toLocaleDateString("en", values)
        var formatedDate = date.replaceAll("/", "-")
        axios.post(url + '/api/insertData' , { "date":formatedDate,"cases" : casesAdd, "admissions" : admissionsAdd, "deaths" : deathsAdd}
            
        )
        console.log({"date":formatedDate,"cases" : casesAdd, "admissions" : admissionsAdd, "deaths" : deathsAdd});
    }

    var values = {
        year: "numeric",
        month: "2-digit",
        day: "2-digit"
    };
    return (
        <>
            <div className="row " style={{ background: "#e0f7fa" }}>
                <h1>Covid-19 Tracker</h1>
            </div>
            <div className="row">

                <div className="col-md-4">
                    <div className="row">
                        <div className="col-md-6">
                            <h2>Search</h2>
                            <Form>
                                <Form.Group widths='equal'>
                                    <p >Start Date</p>
                                    <DatePicker
                                        selected={start_date}
                                        onChange={date => setStartDate(date)}
                                    />
                                    <br />
                                    <p >End Date</p>
                                    <DatePicker
                                        selected={end_date}
                                        onChange={date => setEndDate(date)}
                                    />
                                </Form.Group>
                                < br />
                                <Form.Group widths='equal'>
                                    <div style={{ display: "flex", justifyContent: "center" }}>
                                        <Checkbox id="allData" label='All Data' />
                                        <Checkbox id="mean" label='Mean' />
                                        <Checkbox id="median" label='Median' />
                                    </div>

                                </Form.Group>
                                < br />
                                <Form.Field>
                                    <Button

                                        onClick={(event) => {
                                            event.preventDefault();
                                            if (document.getElementById("allData").checked) {
                                                getAllData();
                                            }
                                            if (document.getElementById("mean").checked) {
                                                getMean();
                                            }
                                            if (document.getElementById("median").checked) {
                                                getMedian();
                                            }
                                        }
                                        }
                                    >
                                        Submit
                            </Button>

                                </Form.Field>
                            </Form>
                        </div>

                        <div className="col-md-6">
                            <h2>Delete</h2>
                            <Form>
                                <Form.Group widths='equal'>
                                    <p >Start Date</p>
                                    <DatePicker
                                        selected={timeStamp}
                                        onChange={date => setTimeStamp(date)}
                                    />
                                </Form.Group>
                                < br />
                                <Form.Field>
                                    <Button

                                        onClick={(event) => {
                                            event.preventDefault();
                                            delteData();
                                        }
                                        }
                                    >
                                        Submit
                            </Button>

                                </Form.Field>
                            </Form>
                        </div>

                    </div>
                    < hr />
                    <div className="row">
                        <div className="col-md-6">
                            <h2>Add</h2>
                            <Form>
                                <p >Date</p>
                                <DatePicker
                                    selected={dateAdd}
                                    onChange={date => setDateAdd(date)}
                                />
                                < br />
                                <Form.Group widths='equal'>
                                    <Form.Input
                                        fluid label='Cases'
                                    value={casesAdd}
                                    onChange={e => setCasesAdd(e.target.value)}
                                    />
                                    <Form.Input
                                        fluid label='Admissions'
                                    value={admissionsAdd}
                                    onChange={e => setAdmissionsAdd(e.target.value)}
                                    />
                                    <Form.Input
                                        fluid label='Deaths'
                                    value={deathsAdd}
                                    onChange={e => setDeathsAdd(e.target.value)}
                                    />

                                </Form.Group>
                                < br />
                                <Form.Field>
                                    <Button

                                        onClick={(event) => {
                                            event.preventDefault();
                                            addData();
                                        }
                                        }
                                    >
                                        Submit
                            </Button>

                                </Form.Field>
                            </Form>
                        </div>

                        <div className="col-md-6">
                            <h2>Edit</h2>
                            <Form>
                                <Form.Group widths='equal'>
                                    <p >Start Date</p>
                                    <DatePicker
                                        selected={dateEdit}
                                        onChange={date => setDateEdit(date)}
                                    />
                                </Form.Group>
                                <Form.Group widths='equal'>
                                    <Form.Input
                                        fluid label='Cases'
                                        value={casesEdit}
                                        onChange={e => setCasesEdit(e.target.value)}
                                    />
                                    <Form.Input
                                        fluid label='Admissions'
                                        value={admissionsEdit}
                                        onChange={e => setAdmissionsEdit(e.target.value)}
                                    />
                                    <Form.Input
                                        fluid label='Deaths'
                                        value={deathsEdit}
                                        onChange={e => setDeathsEdit(e.target.value)}
                                    />

                                </Form.Group>
                                < br />
                                <Form.Field>
                                    <Button

                                        onClick={(event) => {
                                            event.preventDefault();
                                            alterData();
                                        }
                                        }
                                    >
                                        Submit
                            </Button>

                                </Form.Field>
                            </Form>

                        </div>
                    </div>

                </div>




                <div className="col-md-6">
                    <Line
                        data={{
                            labels: dates,
                            datasets: [
                                {
                                    label: 'Active Cases',
                                    // fill: false,
                                    // lineTension: 0.5,
                                    backgroundColor: 'rgba(75,192,192,1)',
                                    borderColor: 'rgba(75,192,192,1)',
                                    borderWidth: 2,
                                    data: covidCases
                                },
                                {
                                    label: 'Admissions',
                                    // fill: false,
                                    // lineTension: 0.5,
                                    backgroundColor: 'rgba(255, 255, 88, 1)',
                                    borderColor: 'rgba(255, 255, 88, 1)',
                                    borderWidth: 2,
                                    data: covidAdmissions
                                },
                                {
                                    label: 'Deaths',
                                    // fill: false,
                                    // lineTension: 0.5,
                                    backgroundColor: 'rgba(255, 0, 0, 1)',
                                    borderColor: 'rgba(255, 0, 0, 1)',
                                    borderWidth: 2,
                                    data: covidDeaths
                                }
                            ]
                        }}
                    />

                </div>
                <div className="col-md-2">

                    <h1>Mean</h1>
                    <p>Cases:{casesMean}</p>
                    <p>Admissions:{admissionsMean}</p>
                    <p>Deaths:{deathsMean}</p>
                    <hr />
                    <h1>Median</h1>
                    <p>Cases:{casesMedian}</p>
                    <p>Admissions:{admissionsMedian}</p>
                    <p>Deaths:{deathsMedian}</p>

                </div>
            </div>
        </>
    );
}
